
.menu, .el-menu {
  z-index: 11;
    height: 100%;
}
.container {
    padding-top: 80px;
    height: 100%;
}

header {
  background-color: rgb(32, 160, 255);
  height: 60px;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 0 20px;
  z-index: 1;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.header-logo {
  display: inline-block;
  vertical-align: middle;
}
.header-operations {
  display: inline-block;
  float: right;
  padding-right: 30px;
  height: 100%;
}
.header-operations li {
  color: #fff;
  display: inline-block;
  vertical-align: middle;
  padding: 0 10px;
  margin: 0 10px;
  line-height: 60px;
  cursor: pointer;
}
.header-operations li:first-child{
  cursor: default
}
.header-opacity{
  opacity: .7;
}
.header-operations:after, header:after {
  display: inline-block;
  content: "";
  height: 100%;
  vertical-align: middle;
}


























